'use strict';

import {openModal, closeModal} from './modal';
import {postData} from '../services/services';

// 
function forms () {
    //    
    const forms = document.querySelectorAll('form');

    //     
    const MESSAGES = {
        loading: 'img/form/spinner.svg',
        success: '!     ',
        error: '-   ...'
    };

    //      
    const showThanksModal = (message) => {
        //   
        const modalElem = document.querySelector('.modal');
        //    
        const prevModalDialogElem = document.querySelector('.modal__dialog');
        //   
        prevModalDialogElem.classList.add('hide');
        
        //    
        const thanksModal = document.createElement('div');
        thanksModal.classList.add('modal__dialog');
        thanksModal.innerHTML = `
        <div class="modal__content">
        <div data-close class="modal__close">&times;</div>
        <div class="modal__title">${message}</div>
        </div>
        `;
        //       
        modalElem.append(thanksModal);
        //  
        openModal('.modal');

        //  4       ,      
        setTimeout(() => {
            thanksModal.remove();
            prevModalDialogElem.classList.remove('hide');
            closeModal('.modal');
        }, 4000);
    };

    //        
    const bindPostData = (form) => {
        form.addEventListener('submit', (evt) => {
            evt.preventDefault();

            //  ,    
            const statusMessage = document.createElement('img');
            statusMessage.src = MESSAGES.loading;
            statusMessage.style.cssText = `
                display: block;
                margin: 0 auto;
            `;
            form.insertAdjacentElement('afterend', statusMessage);

            //   FormData  
            let formData = new FormData(form);
            //  FormData  JSON
            const formDataJson = JSON.stringify(Object.fromEntries(formData.entries()));

            //  
            postData('http://localhost:3000/requests', formDataJson)
            .then((data) => {
                console.log(data);
                //       
                showThanksModal(MESSAGES.success);
                statusMessage.src = '';
            })
            .catch(() => {
                //       
                showThanksModal(MESSAGES.error);
                statusMessage.src = '';
            })
            .finally(() => {
                //  
                form.reset();
            });
        });
    };

    //       
    forms.forEach((form) => {
        bindPostData(form);
    });
}

export default forms;